import { Component, OnInit, Input } from '@angular/core';
import { GithubService } from '../../services/github.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatDialogConfig} from '@angular/material/dialog';
import { UsersReposComponent } from '../users-repos/users-repos.component';
import { RepositoryListModalComponent } from '../repository-list-modal/repository-list-modal.component';

@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.css']
})
export class UsersListComponent implements OnInit {

  public errorMessage;
  public userList: any;
  public reposdata: any;
  public show = false;
  @Input() userName; any;
 
  constructor(private gitusersService: GithubService, public dialog: MatDialog ) { }

  ngOnInit() {
    this.gitusersService.getUser()
    .subscribe(data => {
      this.userList = data;
    }, error => this.errorMessage = error);
  }



}
